.locations-dropdown {
  display: flex;
  align-items: flex-start;
  gap: 1.4rem;

  @media (max-width: $mobile) {
    gap: 0.7rem;
  }

  &__pin {
    background-color: $white;
    padding: 1.8rem 2.4rem;
    flex-shrink: 0;

    @media (max-width: $mobile) {
      padding: 1.4rem 1rem;
      width: 4.3rem;
    }
  }

  &__right {
    position: relative;
  }

  .divider {
    width: calc(100% - 4rem);
    margin-left: 2rem;
  }

  &__content {
    position: absolute;
    background-color: $grey;
    opacity: 0;
    pointer-events: none;
    top: 9.6rem;
    left: 0;
    z-index: 7;
    height: 58rem;
    overflow: auto;

    &::-webkit-scrollbar {
      width: 1.4rem;
    }

    &::-webkit-scrollbar-track {
      background: #D9D9D9;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $red;
      border: 0.3rem solid transparent;
      background-clip: content-box;
    }

    &.is-active {
      opacity: 1;
      pointer-events: all;
    }

    @media (max-width: $mobile) {
      top: 5.7rem;
      height: 34rem;
    }
  }

  &__arrow {
    margin-right: 0.7rem;
    margin-top: 0.3rem;

    @media (max-width: $mobile) {
      width: 2.6rem;
      margin-right: .5rem;
      margin-top: .8rem;
    }
  }

  &__item {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
    background-color: $grey;
    min-width: 63.6rem;
    padding: 1.6rem 1.9rem;
    color: $red;
    cursor: pointer;

    @media (max-width: $tablet) {
      min-width: 56.6rem;
    }

    @media (max-width: $mobile) {
      min-width: 27.9rem;
      padding: .7rem 0.7rem 0.5rem 0.9rem;
      gap: 1rem;
    }

    span {
      color: $black;
      font-size: 2rem;

      @media (max-width: $mobile) {
        font-size: 1.4rem;
        line-height: 1.1;
      }
    }

    &--trigger {
      background-color: $white;
      user-select: none;
      min-width: 65rem;

      @media (max-width: $tablet) {
        min-width: 58rem;
      }

      @media (max-width: $mobile) {
        min-width: 29.3rem;
      }
    }

    &.is-active {
      color: $white;
      background-color: $red;

      span {
        color: $white;
      }

      &.locations-dropdown__item--trigger {
        background-color: $white;
        color: $red;

        span {
          color: $black;
        }

        .locations-dropdown__arrow img {
          transform: rotate(180deg);
        }
      }
    }
  }

  &__location-name {
    font-family: $mossport;
    color: currentColor;
    text-transform: uppercase;
    font-size: 4rem;
    line-height: 1.01;
    margin: 0 0 0.2rem;

    @media (max-width: $mobile) {
      font-size: 2.4rem;
      line-height: 1.01;
      margin: 0;
    }
  }

}
